import React, { Component } from 'react'
import { Container, Row, Col } from 'reactstrap'
import axios from 'axios'
import NumberFormat  from 'react-number-format'

class App extends Component {
  
  state = {
    forgalom: "",
    etelNev: ""
  }
    
  componentDidMount() {
    axios.get(`http://localhost:8000/api/forgalom`)
    .then(res => {
      const forgalom = res.data.forgalom 
      this.setState({ forgalom })
    })
    axios.get(`http://localhost:8000/api/legnepszerubb`)
    .then(res => {
      const etelNev = res.data.etelNev 
      this.setState({ etelNev })
    })    
  }
  
  render() {
  
    return (
      <div className="App">
        <Container className="mt-5">
          <Row>
            <Col>
              <h1 className="text-center">Csudijó Étterem - Adminisztrációs oldal</h1>
            </Col>
          </Row> 
          <Row className="m-1">
            <Col xs="5" className="text-white bg-secondary p-2">1. Összesített havi forgalom:</Col>
            <Col xs="7" className="bg-light p-2"><NumberFormat value={this.state.forgalom} displayType="text" thousandSeparator=" "></NumberFormat>{" Ft"}</Col>
          </Row> 
          <Row className="m-1">
            <Col xs="5" className="text-white bg-secondary p-2">2. Legnépszerűbb étel:</Col>
            <Col xs="7" className="bg-light p-2">{this.state.etelNev}</Col>
          </Row>           
        </Container>
      </div>
    );
  }
}

export default App
